.page {
  margin: 0;
  font-family: Arial, sans-serif;
}

.container {
  box-sizing: border-box;
  grid-template-columns: repeat(2, 200px);
  gap: 10px 20px;
  max-width: 410px;
  margin: 0 auto;
  display: grid;
}

@media (width >= 1000px) {
  .container {
    grid-template-columns: repeat(4, 200px);
    max-width: 830px;
  }
}

.person {
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 10px;
  display: flex;
}

.person .photo {
  border-radius: 50%;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  position: relative;
  overflow: hidden;
}

.person .photo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  margin-bottom: 8px;
  position: absolute;
}

.person .rank {
  color: #8b0000;
  z-index: 5;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  top: calc(50% - 16px);
  right: 10px;
}

.person .details .name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
}

.person .details .name .extra {
  display: none;
}

.person:hover {
  background-color: #eee;
}
